import React from 'react'
const AboutContent = () => {
    const Margins  = {
        color:"White",
        fontFamily:"Space Grotesk",
        fontSize: '18px',
        borderColor:"White",
        borderLeft: "2px",
        borderRight: "2px",
    }
    return(
        <>
        <content style = {Margins}>
            <ul class = "hex">
            <p>
                <li>I am an enthusiastic High-schooler (Grade 12 Student) who is interested in learning about new tech and I'm highly passionate about learning principles of the modern-day business world. I love airplanes and I'm very much interested learning about flights and everything related to aviation. Being an avid Formula One fan, I support the Mercedes Petronas AMG F1 Team and the entire sporting fraternity. Cars in-general are beautiful and its obvious that one would enjoy their weekend in a vintage car. If you guys see a Mercedes 500K anywhere please reach out through the Contact me.</li>
            </p>
            <p>
                <li>
                    Apart from Dev, Formula One and Tech, I do workout sometimes. Being locked at home for sometime because of the pandemic, I leant how to cook and enjoy the scenery around.Its amazing to re-learn old perspectives and explore new. 
                </li>
            </p>
            </ul>
        </content>
    </>
    )
}
export default AboutContent;