import React from 'react'
import { Grid, Typography } from '@material-ui/core'
import { BlogCard, CardInfo, ExternalLinks, GridContainer, HeaderThree, TitleUnderline, Tag, TagList, TitleContent, UtilityList, Img, SectionTag } from '../Projects/ProjectStyles.jsx';
import { SectionDivider } from '../PageCenter/SectionDivider.jsx';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import {Data} from '../Data/data.js'
import { data } from 'jquery';

const ProjectConst = () => {
    const Margins  = {
        color:"White",
        fontFamily:"Space Grotesk",
        fontSize: '18px',
        borderColor:"White",
        borderLeft: "2px",
        borderRight: "2px",
    }
    return(
    <>
        {Data.map((p,i) => {return (
           <GridContainer>
           <BlogCard key = {i}>
               <TitleContent>
               <br></br>
               <Typography variant = "h5" style = {{color:'White', fontFamily:'Space Grotesk'}}>
                   {p.title}
               </Typography>
               <TitleUnderline />             
               </TitleContent>            
               <Typography style = {{color:"White", align:'Left', padding:10}}>{p.description}</Typography>
               <section>
               <Grid   container  direction="row"  justify="space-evenly"  alignItems="center">
                   {p.stack.map((t) => {
                       return (<>
                                <Grid Item xs><Typography variant = "h6" style = {{color:"White"}}>{t}</Typography></Grid></>)
                       })}
               <Grid/>

                   </Grid>
               </section>
               <UtilityList>
               <ExternalLinks href="#">Code</ExternalLinks>
               <ExternalLinks href="#">Web</ExternalLinks>
               </UtilityList>
           </BlogCard>
       </GridContainer>  
        )})}
    </>
    )
}
export default ProjectConst;    
